        </view>
        <view class="content_info_label_photo">
        <view class="content_info_label_photo_name">照片：</view> 
        <view class="content_info_label_photo_content">
				  <image v-for="item in imgListArray" :key="item" class="image" mode="scaleToFill" @click="previewImg()" :src='item' />
        </view>
      </view>
      </view>
    </view>
   </view>
</template>
<script>
import datetimePicker from '../../components/datatime-picker/datetime-picker.vue';
import UploadImage from '../../components/upload-image/upload-image.vue';

  export default {
    components: {
      datetimePicker,
      UploadImage
    },
    data() {
      return {
        title: 'input',
        defaultValue: '', // 默认值
        dataList: [],
        imgListArray: [],
				types: 'image',
        params: {}
      }
    },
    onLoad(options) {
      console.log(options.params)
      this.params = JSON.parse(decodeURIComponent(options.params))
      console.log(this.params.fileUrls) 
      this.imgListArray = this.params.fileUrls
      let type = this.params.type
      console.log('type =>',type)
      if(type == 1){
        this.params.typeName = '车辆事故'
      }else if(type == 2){
        this.params.typeName = '车辆故障'
      }else if(type == 3){
        this.params.typeName = '应急停车'
      }
      
		},
    methods: {
      previewImg() {
        wx.previewImage({
					urls: this.imgListArray, //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: this.imgListArray[0], // 当前显示图片的http链接，默认是第一个
					success: function(res) {},
					fail: function(res) {},
					complete: function(res) {},
				})
		  },
    },
  }
</script>

